<template>
    <div v-show="node.id || line.from">
        <div class="ef-node-form">
            <div class="ef-node-form-header">
                
            </div>
            <div class="ef-node-form-body">
                <el-form :model="node" ref="dataForm" label-width="80px" v-show="type === 'node'">
                  <el-form-item label="id">
                    <el-input v-model="node.id" disabled></el-input>
                  </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="node.type" :disabled="true"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="node.name"></el-input>
                    </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="node.message">
                      <el-option v-for="(item) in childProjects" :key="item.key" :value="item.key" :label="item.projectName"></el-option>
                    </el-select>
                    <el-button v-if="sub" type="text" @click="$emit('openSub')"></el-button>
                  </el-form-item>
                  <template v-if="node.type === 'message' || node.type === 'choose'">
                    <el-form-item label="">
                      <el-switch
                        v-model="node.disableCompress" :active-value="false" :inactive-value="true"
                        active-color="#13ce66"
                        inactive-color="#ff4949" ></el-switch>
                    </el-form-item>
                    <el-form-item label="">
                      <el-select v-model="node.hero"    filterable @change="heroChange"
                                 allow-create
                                 default-first-option>
                        <template v-if="data.externHeroes">
                          <el-option v-for="hero in data.externHeroes" :key="hero" :value="hero" :label="hero"></el-option>
                        </template>
                        <el-option :value="$config.defaultHero" :label="$config.defaultHero"></el-option>
                        <el-option v-for="hero in $config.allHeroes" :key="hero" :value="'(' + hero + ')'" :label="hero"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="" >
                      <el-input
                        type="textarea"
                        :rows="4"
                        placeholder=""
                        v-model="node.message">
                      </el-input>
                    </el-form-item>
                  </template>
                  <el-form-item label="" v-if="node.type === 'code'">
                    <el-input v-model="node.code" placeholder="OW"
                              type="textarea"
                              :rows="4"></el-input>
                  </el-form-item>
                  <el-form-item label="" v-if="node.type === 'ow-if'">
                    <el-input v-model="node.code" placeholder="OW if"></el-input>
                  </el-form-item>
                  <template  v-if="node.type === 'choose'">
                    <el-form-item label="1">
                      <el-input
                        placeholder=""
                        v-model.trim="node.choose1" />
                    </el-form-item>
                    <el-form-item label="2">
                      <el-input
                        placeholder=""
                        v-model.trim="node.choose2" />
                    </el-form-item>
                    <el-form-item label="3">
                      <el-input
                        placeholder=""
                        v-model.trim="node.choose3" />
                    </el-form-item>
                    <el-form-item label="4">
                      <el-input
                        placeholder=""
                        v-model.trim="node.choose4" />
                    </el-form-item>
                    <el-form-item label="5">
                      <el-input
                        placeholder=""
                        v-model.trim="node.choose5" />
                    </el-form-item>
                  </template>
                  <!--                    <el-form-item label="left">
                                          <el-input v-model="node.left" :disabled="true"></el-input>
                                      </el-form-item>
                                      <el-form-item label="top">
                                          <el-input v-model="node.top" :disabled="true"></el-input>
                                      </el-form-item>
                                      <el-form-item label="ico">
                                          <el-input v-model="node.ico"></el-input>
                                      </el-form-item>
                                      <el-form-item label="">
                                          <el-select v-model="node.state" placeholder="">
                                              <el-option
                                                      v-for="item in stateList"
                                                      :key="item.state"
                                                      :label="item.label"
                                                      :value="item.state">
                                              </el-option>
                                          </el-select>
                                      </el-form-item>-->
                    <el-form-item>
                      <el-button icon="el-icon-close" type="danger" :disabled="node.type === 'start'" @click="deleteNode"></el-button>
                      <el-button type="primary" icon="el-icon-check" @click="save"></el-button>
                    </el-form-item>
                  <el-form-item>
                  </el-form-item>
                </el-form>

                <el-form :model="line" ref="dataForm" label-width="80px" v-show="type === 'line'">
                    <el-form-item label="">
                        <el-input v-model="line.label"></el-input>
                    </el-form-item>
                  <el-form-item label="" v-if="line.nodeType === 'choose'">
                    <el-select v-model="line.choose" clearable placeholder="">
                      <el-option value="c1" label="1"></el-option>
                      <el-option value="c2" label="2"></el-option>
                      <el-option value="c3" label="3"></el-option>
                      <el-option value="c4" label="4"></el-option>
                      <el-option value="c5" label="5"></el-option>
                    </el-select>
                  </el-form-item>
                    <el-form-item>
                      <el-button icon="el-icon-close" type="danger" @click="deleteLine"></el-button>
                        <el-button type="primary" icon="el-icon-check" @click="saveLine"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <!--            <div class="el-node-form-tag"></div>-->
        </div>
    </div>

</template>

<script>
    import { cloneDeep } from 'lodash'

    export default {
        props:{
          sub: Boolean,
          childProjects: []
        },
        data() {
            return {
                visible: true,
                // node  line
                type: 'node',
                node: {},
                line: {},
                data: {},
                stateList: [{
                    state: 'success',
                    label: ''
                }, {
                    state: 'warning',
                    label: ''
                }, {
                    state: 'error',
                    label: ''
                }, {
                    state: 'running',
                    label: ''
                }]
            }
        },
        methods: {
            heroChange(val){
              this.$emit('heroChange',val)
            },
            /**
             * ID
             * @param data
             * @param id
             */
            nodeInit(data, id) {
                this.type = 'node'
                this.data = data
                data.nodeList.filter((node) => {
                    if (node.id === id) {
                        this.node = cloneDeep(node)
                    }
                })
            },
            lineInit(line) {
                this.type = 'line'
                this.line = line
            },
            // 
            saveLine() {
                this.$emit('setLineLabel', this.line.from, this.line.to, this.line.label,this.line.choose)
            },
            save() {
                this.data.nodeList.filter((node) => {
                    if (node.id === this.node.id) {
                        node.name = this.node.name
                        node.left = this.node.left
                        node.top = this.node.top
                        node.ico = this.node.ico
                        node.state = this.node.state
                        node.hero = this.node.hero
                        node.message = this.node.message ? this.node.message.trim() : this.node.message
                        node.choose1 = this.node.choose1
                      node.choose2 = this.node.choose2
                      node.choose3 = this.node.choose3
                      node.choose4 = this.node.choose4
                      node.choose5 = this.node.choose5
                      node.code = this.node.code
                      node.disableCompress = this.node.disableCompress
                        this.$emit('repaintEverything')
                    }
                })
            },
          deleteNode(){
              if(this.node.id){
                this.$emit('deleteNode',this.node.id)
              }
          },
          deleteLine(){
              if(this.line.from && this.line.to){
                this.$emit('deleteLine',{
                  from: this.line.from,
                  to: this.line.to
                })
              }
          }
        }
    }
</script>

<style>
    .el-node-form-tag {
        position: absolute;
        top: 50%;
        margin-left: -15px;
        height: 40px;
        width: 15px;
        background-color: #fbfbfb;
        border: 1px solid rgb(220, 227, 232);
        border-right: none;
        z-index: 0;
    }
</style>
