<template>
    <div v-if="easyFlowVisible" style="height: calc(100vh);">
        <el-row>
            <!---->
            <el-col :span="24">
                <div class="ef-tooltar">
                  <el-dropdown  @command="handleCommand">
                    <span class="el-dropdown-link">
                      {{projectName}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item command="new"></el-dropdown-item>
                      <el-dropdown-item command="save"></el-dropdown-item>
                      <el-dropdown-item command="open"></el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                  <el-divider direction="vertical"></el-divider>

                  <!--                    <el-button type="text" icon="el-icon-delete" size="large" @click="deleteElement" :disabled="!this.activeElement.type"></el-button>
                                      <el-divider direction="vertical"></el-divider>-->
                  <el-upload action="" :auto-upload="false" :show-file-list="false" :on-change="fileChange" accept=".json" style="display: inline-block">
                    <el-button type="text" ref="openBtn" icon="el-icon-upload2" size="large"></el-button>
                  </el-upload>
                  <el-divider direction="vertical"></el-divider>
                    <el-button type="text" icon="el-icon-download" size="large" @click="downloadData"></el-button>
                  <el-button type="text" v-if="parentData" @click="returnProject"></el-button>
                  <el-button type="text" v-else @click="$refs.sub.open()"></el-button>
<!--                    <el-divider direction="vertical"></el-divider>
                    <el-button type="text" icon="el-icon-plus" size="large" @click="zoomAdd"></el-button>
                    <el-divider direction="vertical"></el-divider>
                    <el-button type="text" icon="el-icon-minus" size="large" @click="zoomSub"></el-button>-->
                    <div style="float: right;margin-right: 5px">
<!--                        <el-button type="info" plain round icon="el-icon-document" @click="dataInfo" size="mini"></el-button>-->
<!--                        <el-button type="primary" plain round @click="dataReloadA" icon="el-icon-refresh" size="mini">A</el-button>
                        <el-button type="primary" plain round @click="dataReloadB" icon="el-icon-refresh" size="mini">B</el-button>
                        <el-button type="primary" plain round @click="dataReloadC" icon="el-icon-refresh" size="mini">C</el-button>
                        <el-button type="primary" plain round @click="dataReloadD" icon="el-icon-refresh" size="mini"></el-button>
                        <el-button type="primary" plain round @click="dataReloadE" icon="el-icon-refresh" size="mini"></el-button>-->
                      <el-button type="primary" plain round icon="el-icon-document-checked" @click="downloadData"></el-button>
                      <el-button type="primary" plain round icon="el-icon-check" @click="exportCode"></el-button>
                      <el-button type="info" plain round icon="el-icon-document" @click="openHelp" size="mini"></el-button>
                    </div>
                </div>
            </el-col>
        </el-row>
        <div style="display: flex;height: calc(100% - 47px);">
            <div style="width: 230px;border-right: 1px solid #dce3e8;">
                <node-menu @addNode="addNode" ref="nodeMenu"></node-menu>
            </div>
            <div id="efContainer" ref="efContainer" class="container" v-flowDrag>
                <template v-for="node in data.nodeList">
                    <flow-node
                            :id="node.id"
                            :key="node.id"
                            :node="node"
                            :activeElement="activeElement"
                            @changeNodeSite="changeNodeSite"
                            @nodeRightMenu="nodeRightMenu"
                            @clickNode="clickNode"
                    >
                    </flow-node>
                </template>
                <!--  -->
                <div style="position:absolute;top: 2000px;left: 2000px;">&nbsp;</div>
            </div>
            <!--  -->
            <div style="width: 300px;border-left: 1px solid #dce3e8;background-color: #FBFBFB">
                <flow-node-form ref="nodeForm" :sub="!parentData" :child-projects="childProjects" @setLineLabel="setLineLabel" @openSub="$refs.sub.open()"
                                @heroChange="heroChange"
                                @deleteNode="deleteNode" @deleteLine="onDeleteLine"
                                @repaintEverything="repaintEverything"></flow-node-form>
            </div>
        </div>
        <!--  -->
        <flow-info v-if="flowInfoVisible" ref="flowInfo" :data="data"></flow-info>
        <flow-help v-if="flowHelpVisible" ref="flowHelp"></flow-help>
      <el-dialog
        title="OWMOD GAL Designer"
        :visible.sync="modal" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false"
        width="30%">
        <el-form>
          <el-form-item>
            <el-button type="text" @click="createProject" style="font-size: 18px;color:#409EFF"></el-button>
          </el-form-item>
          <el-form-item>
            <el-upload action="" :auto-upload="false" :show-file-list="false" :on-change="fileChange" accept=".json" style="display: inline-block">
              <el-button type="text" style="font-size: 18px;color:#6cf"></el-button>
            </el-upload>
          </el-form-item>
          <el-form-item>
            <el-button type="text" @click="openHelp" style="font-size: 18px;color:#909399"></el-button>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
      </span>
      </el-dialog>
      <el-dialog title="" :visible.sync="createModal" width="30%" :close-on-click-modal="false" :close-on-press-escape="false">
        <el-form>
          <el-form-item label="">
            <el-input v-model.trim="createProjectData"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="createModal = false"></el-button>
          <el-button @click="doCrateProject" type="primary"></el-button>

        </div>
      </el-dialog>
      <el-dialog title="" :visible.sync="codeModal" width="50%" :close-on-click-modal="false" :close-on-press-escape="false">
        <el-tabs v-model="codeTab">
          <el-tab-pane label="" name="code">
            <pre class="code-pre">{{code}}</pre>
          </el-tab-pane>
          <el-tab-pane label="" name="trigger">
            <pre class="code-pre">{{triggerCode}}</pre>
          </el-tab-pane>
          <el-tab-pane label="" name="all">
            <pre class="code-pre">{{modcode}}</pre>
          </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
          <el-button type="text" @click="copyCode"></el-button>
          <el-button @click="codeHelp"></el-button>
          <el-button @click="codeModal = false" type="primary"></el-button>
        </div>
      </el-dialog>
      <sub-project ref="sub" @editSub="editSub" :data="data.childProjects"/>
    </div>

</template>

<script>
    import draggable from 'vuedraggable'
    // import { jsPlumb } from 'jsplumb'
    // jsplumb
    import './jsplumb'
    import { easyFlowMixin } from '@/components/ef/mixins'
    import flowNode from '@/components/ef/node'
    import nodeMenu from '@/components/ef/node_menu'
    import FlowInfo from '@/components/ef/info'
    import FlowHelp from '@/components/ef/help'
    import FlowNodeForm from './node_form'
    import lodash from 'lodash'
    import { getDataA } from './data_A'
    import { getDataB } from './data_B'
    import { getDataC } from './data_C'
    import { getDataD } from './data_D'
    import { getDataE } from './data_E'
    import { ForceDirected } from './force-directed'
    import { v4 as uuidv4 } from 'uuid';
    import {getData} from './data'
    import { saveAs } from 'file-saver';
    import gencode from "@/mod/gencode";
    import modcode from "@/mod/modcode";
    import SubProject from "@/components/ef/subProject";



    export default {
        computed:{
          childProjects(){
            if(this.parentData){
              const {data,key} = this.parentData
              return data.childProjects.filter(item => item.key !== key)
            }
            return this.data.childProjects
          }
        },
        data() {
            return {
              triggerCode: '',
              code: '',
              modcode:modcode,
              codeModal: false,
              codeTab: 'code',
               projectName: '',
              modal: true,
              createModal: false,
              createProjectData: this.$config.defaultProjectName,
                // jsPlumb 
                jsPlumb: null,
                // 
                easyFlowVisible: true,
                // 
                flowInfoVisible: false,
                // 
                loadEasyFlowFinish: false,
                flowHelpVisible: false,
                // 
                data: {},
                // 
                activeElement: {
                    //  node line
                    type: undefined,
                    // ID
                    nodeId: undefined,
                    // ID
                    sourceId: undefined,
                    targetId: undefined
                },
                zoom: 0.5,
                parentData: null
            }
        },
        // 
        mixins: [easyFlowMixin],
        components: {
          SubProject,
            draggable, flowNode, nodeMenu, FlowInfo, FlowNodeForm, FlowHelp
        },
        directives: {
            'flowDrag': {
                bind(el, binding, vnode, oldNode) {
                    if (!binding) {
                        return
                    }
                    el.onmousedown = (e) => {
                        if (e.button == 2) {
                            // 
                            return
                        }
                        //  
                        let disX = e.clientX
                        let disY = e.clientY
                        el.style.cursor = 'move'

                        document.onmousemove = function (e) {
                            // 
                            e.preventDefault()
                            const left = e.clientX - disX
                            disX = e.clientX
                            el.scrollLeft += -left

                            const top = e.clientY - disY
                            disY = e.clientY
                            el.scrollTop += -top
                        }

                        document.onmouseup = function (e) {
                            el.style.cursor = 'auto'
                            document.onmousemove = null
                            document.onmouseup = null
                        }
                    }
                }
            }
        },
        mounted() {
            this.jsPlumb = jsPlumb.getInstance()
            this.$nextTick(() => {
                this.dataReload(getData())
            })
          window.onbeforeunload=(e) => {
            if(!this.modal){
              var e = window.event||e;
              e.returnValue=("");
            }
          }
        },
        methods: {
            // 
            getUUID() {
                return uuidv4()
            },
            jsPlumbInit() {
                this.jsPlumb.ready(() => {
                    // 
                    this.jsPlumb.importDefaults(this.jsplumbSetting)
                    // jsPlumb
                    this.jsPlumb.setSuspendDrawing(false, true);
                    // 
                    this.loadEasyFlow()
                    // , https://www.cnblogs.com/ysx215/p/7615677.html
                    this.jsPlumb.bind('click', (conn, originalEvent) => {
                        this.activeElement.type = 'line'
                        this.activeElement.sourceId = conn.sourceId
                        this.activeElement.targetId = conn.targetId
                        this.$refs.nodeForm.lineInit({
                            from: conn.sourceId,
                            to: conn.targetId,
                            label: conn.getLabel(),
                          choose:this.data.lineList.filter(l => l.from ===  conn.sourceId && l.to === conn.targetId)[0].choose,
                          nodeType:this.data.nodeList.filter(node => node.id === conn.sourceId)[0].type
                        })
                    })
                    // 
                    this.jsPlumb.bind("connection", (evt) => {
                        let from = evt.source.id
                        let to = evt.target.id
                        if (this.loadEasyFlowFinish) {
                            this.data.lineList.push({from: from, to: to})
                        }
                    })

                    // 
                    this.jsPlumb.bind("connectionDetached", (evt) => {
                        this.deleteLine(evt.sourceId, evt.targetId)
                    })

                    // 
                    this.jsPlumb.bind("connectionMoved", (evt) => {
                        this.changeLine(evt.originalSourceId, evt.originalTargetId)
                    })

                    // 
                    this.jsPlumb.bind("contextmenu", (evt) => {
                        console.log('contextmenu', evt)
                    })

                    // 
                    this.jsPlumb.bind("beforeDrop", (evt) => {
                        let from = evt.sourceId
                        let to = evt.targetId
                        if (from === to) {
                            this.$message.error('')
                            return false
                        }
                        if (this.hasLine(from, to)) {
                            this.$message.error(',')
                            return false
                        }
                        if (this.hashOppositeLine(from, to)) {
                            this.$message.error('');
                            return false
                        }
                        this.$message.success('')
                        return true
                    })

                    // beforeDetach
                    this.jsPlumb.bind("beforeDetach", (evt) => {
                        console.log('beforeDetach', evt)
                    })
                    this.jsPlumb.setContainer(this.$refs.efContainer)
                })
            },
            // 
            loadEasyFlow() {
                // 
                for (var i = 0; i < this.data.nodeList.length; i++) {
                    let node = this.data.nodeList[i]
                    // 
                    this.jsPlumb.makeSource(node.id, lodash.merge(this.jsplumbSourceOptions, {}))
                    // // 
                    this.jsPlumb.makeTarget(node.id, this.jsplumbTargetOptions)
                    if (!node.viewOnly) {
                        this.jsPlumb.draggable(node.id, {
                            containment: 'parent',
                            stop: function (el) {
                                // 
                                console.log(': ', el)
                            }
                        })
                    }
                }
                // 
                for (var i = 0; i < this.data.lineList.length; i++) {
                    let line = this.data.lineList[i]
                    var connParam = {
                        source: line.from,
                        target: line.to,
                        label: line.label ? line.label : '',
                        connector: line.connector ? line.connector : '',
                        anchors: line.anchors ? line.anchors : undefined,
                        paintStyle: line.paintStyle ? line.paintStyle : undefined,
                    }
                    this.jsPlumb.connect(connParam, this.jsplumbConnectOptions)
                }
                this.$nextTick(function () {
                    this.loadEasyFlowFinish = true
                })
            },
            // 
            setLineLabel(from, to, label,choose) {
                var conn = this.jsPlumb.getConnections({
                    source: from,
                    target: to
                })[0]
                if (!label || label === '') {
                    conn.removeClass('flowLabel')
                    conn.addClass('emptyFlowLabel')
                } else {
                    conn.addClass('flowLabel')
                }
                conn.setLabel({
                    label: label,
                })
                this.data.lineList.forEach(function (line) {
                    if (line.from == from && line.to == to) {
                        line.label = label
                        line.choose = choose
                    }
                })

            },
            // 
            deleteElement() {
                if (this.activeElement.type === 'node') {
                    this.deleteNode(this.activeElement.nodeId)
                } else if (this.activeElement.type === 'line') {
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        var conn = this.jsPlumb.getConnections({
                            source: this.activeElement.sourceId,
                            target: this.activeElement.targetId
                        })[0]
                        this.jsPlumb.deleteConnection(conn)
                    }).catch(() => {
                    })
                }
            },
            // 
            deleteLine(from, to) {
                this.data.lineList = this.data.lineList.filter(function (line) {
                    if (line.from == from && line.to == to) {
                        return false
                    }
                    return true
                })
            },
            // 
            changeLine(oldFrom, oldTo) {
                this.deleteLine(oldFrom, oldTo)
            },
            // 
            changeNodeSite(data) {
                for (var i = 0; i < this.data.nodeList.length; i++) {
                    let node = this.data.nodeList[i]
                    if (node.id === data.nodeId) {
                        node.left = data.left
                        node.top = data.top
                    }
                }
            },
            /**
             * 
             * @param evt
             * @param nodeMenu 
             * @param mousePosition 
             */
            addNode(evt, nodeMenu, mousePosition) {
                var screenX = evt.originalEvent.clientX, screenY = evt.originalEvent.clientY
                let efContainer = this.$refs.efContainer
                var containerRect = efContainer.getBoundingClientRect()
                var left = screenX, top = screenY
                // 
                if (left < containerRect.x || left > containerRect.width + containerRect.x || top < containerRect.y || containerRect.y > containerRect.y + containerRect.height) {
                    this.$message.error("")
                    return
                }
                left = left - containerRect.x + efContainer.scrollLeft
                top = top - containerRect.y + efContainer.scrollTop
                // 
                left -= 85
                top -= 16
                var nodeId = this.getUUID()
                // 
                var origName = nodeMenu.name
                var nodeName = origName
                var index = 1
                while (index < 10000) {
                    var repeat = false
                    for (var i = 0; i < this.data.nodeList.length; i++) {
                        let node = this.data.nodeList[i]
                        if (node.name === nodeName) {
                            nodeName = origName + index
                            repeat = true
                        }
                    }
                    if (repeat) {
                        index++
                        continue
                    }
                    break
                }
                var node = {
                    id: nodeId,
                    name: nodeName,
                    type: nodeMenu.type,
                    left: left + 'px',
                    top: top + 'px',
                    ico: nodeMenu.ico,
                    state: 'success',
                    hero: this.$config.defaultHero
                }
                /**
                 * 
                 */
                this.data.nodeList.push(node)
                this.$nextTick(function () {
                    this.jsPlumb.makeSource(nodeId, this.jsplumbSourceOptions)
                    this.jsPlumb.makeTarget(nodeId, this.jsplumbTargetOptions)
                    this.jsPlumb.draggable(nodeId, {
                        containment: 'parent',
                        stop: function (el) {
                            // 
                            console.log(': ', el)
                        }
                    })
                })
            },
            /**
             * 
             * @param nodeId ID
             */
            deleteNode(nodeId) {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }).then(() => {
                    /**
                     * 
                     */
                    this.data.nodeList = this.data.nodeList.filter(function (node) {
                        if (node.id === nodeId) {
                            // 
                            // node.show = false
                            return false
                        }
                        return true
                    })
                    this.$nextTick(function () {
                        this.jsPlumb.removeAllEndpoints(nodeId);
                    })
                }).catch(() => {
                })
                return true
            },
            clickNode(nodeId) {
                this.activeElement.type = 'node'
                this.activeElement.nodeId = nodeId
                this.$refs.nodeForm.nodeInit(this.data, nodeId)
            },
            // 
            hasLine(from, to) {
                for (var i = 0; i < this.data.lineList.length; i++) {
                    var line = this.data.lineList[i]
                    if (line.from === from && line.to === to) {
                        return true
                    }
                }
                return false
            },
            // 
            hashOppositeLine(from, to) {
                return this.hasLine(to, from)
            },
            nodeRightMenu(nodeId, evt) {
                this.menu.show = true
                this.menu.curNodeId = nodeId
                this.menu.left = evt.x + 'px'
                this.menu.top = evt.y + 'px'
            },
            repaintEverything() {
                this.jsPlumb.repaint()
            },
            // 
            dataInfo() {
                this.flowInfoVisible = true
                this.$nextTick(function () {
                    this.$refs.flowInfo.init()
                })
            },
            // 
            dataReload(data) {
              return new Promise((resolve => {
                this.easyFlowVisible = false
                this.data.nodeList = []
                this.data.lineList = []
                this.$nextTick(() => {
                  data = lodash.cloneDeep(data)
                  this.easyFlowVisible = true
                  this.data = data
                  this.$nextTick(() => {
                    this.jsPlumb = jsPlumb.getInstance()
                    this.$nextTick(() => {
                      this.jsPlumbInit()
                      resolve()
                    })
                  })
                })
              }))

            },
            // dataA
            dataReloadA() {
                this.dataReload(getDataA())
            },
            // dataB
            dataReloadB() {
                this.dataReload(getDataB())
            },
            // dataC
            dataReloadC() {
                this.dataReload(getDataC())
            },
            // dataD
            dataReloadD() {
                this.dataReload(getDataD())
            },
            // dataE
            dataReloadE() {
                let dataE = getDataE()
                let tempData = lodash.cloneDeep(dataE)
                let data = ForceDirected(tempData)
                this.dataReload(data)
                this.$message({
                    message: '',
                    type: 'warning'
                });
            },
            zoomAdd() {
                if (this.zoom >= 1) {
                    return
                }
                this.zoom = this.zoom + 0.1
                this.$refs.efContainer.style.transform = `scale(${this.zoom})`
                this.jsPlumb.setZoom(this.zoom)
            },
            zoomSub() {
                if (this.zoom <= 0) {
                    return
                }
                this.zoom = this.zoom - 0.1
                this.$refs.efContainer.style.transform = `scale(${this.zoom})`
                this.jsPlumb.setZoom(this.zoom)
            },
            // 
            downloadData() {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }).then(() => {
                  this.$message.success(",...")
                  let data = this.data
                  if(this.parentData){
                    this.ensureParent()
                    data = lodash.cloneDeep(this.parentData.data)
                  }
                  let cache = []
                  const blob = new Blob([
                    JSON.stringify({
                      data:  data,
                      version: this.$config.dataFileVersion,
                      project: 'designer',
                      projectName: this.projectName
                    },
                      function(key, value) {
                        if (typeof value === 'object' && value !== null) {
                          if (cache.indexOf(value) !== -1) {
                            // 
                            return;
                          }
                          // 
                          cache.push(value);
                        }
                        return value;
                      }
                    )
                  ], {type: "text/plain;charset=utf-8"});
                  cache = null;
                  saveAs(blob, this.projectName +  ".json");

                }).catch(() => {
                })
            },
            openHelp() {
                this.flowHelpVisible = true
                this.$nextTick(function () {
                    this.$refs.flowHelp.init()
                })
            },
            onDeleteLine({from,to}){
              var conn = this.jsPlumb.getConnections({
                source: from,
                target: to
              })[0]
              this.jsPlumb.deleteConnection(conn)
            },
          fileChange(file){
            const reader = new FileReader();//FileReader
            reader.readAsText(file.raw, "UTF-8");//
            reader.onload = (evt) => {
              const fileString = evt.target.result;
              try{
                this.loadFileData(fileString)
              }catch (e) {
                this.$message.error('' + e.message)
              }
            }
          },
          heroChange(val){
              if(this.$config.allHeroes.indexOf(val) === -1 && this.data.externHeroes.indexOf(val) === -1){
                this.data.externHeroes.push(val)
              }
          },
          loadFileData(fileString){
              const data = JSON.parse(fileString)
              if(data.project !== 'designer'){
                throw new Error('')
              }
              if(data.version === 100){
                data.data.nodeList.forEach(node => {
                  node.hero = this.$config.defaultHero
                })
              }
              if(data.version < 103){
                data.data.externHeroes = []
              }
              if(data.version < 104){
                data.data.childProjects = []
              }

              this.dataReload(data.data)
              this.projectName = data.projectName
            if(this.modal){
              this.modal = false
            }
          },

          createProject(){

            this.createProjectData = this.$config.defaultProjectName
            this.createModal = true
          },
          doCrateProject(){
              if(!this.createProjectData.length){
                this.$message.error('')
                return
              }
              this.createModal = false
              this.$nextTick(() => {
                this.projectName = this.createProjectData
                this.modal = false
              })
          },
          handleCommand(cmd){
              if(cmd === 'save') {
                this.downloadData()
              }else if(cmd === 'open'){
                this.$refs.openBtn.$el.click()
              }else if(cmd === 'new'){
                this.$confirm('', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning',
                  closeOnClickModal: false
                }).then(() => {
                  this.dataReload(getData())
                  this.createModal = true
                })
              }
          },
          exportCode(){
              let data = this.data
              if(this.parentData && confirm('')){
                this.ensureParent()
                data = lodash.cloneDeep(this.parentData.data)
              }
              try{
                const result =   gencode(data)
                this.code = result.code
                this.triggerCode = result.trigger
              }catch (e){
                console.error(e)
                this.$message.error('' + e.message)
                return
              }
              this.codeTab = 'code'
              this.codeModal = true
          },
          copyCode(){
              let toCopy = ''
              if(this.codeTab === 'all'){
                toCopy = this.modcode
              }else if(this.codeTab === 'code'){
                toCopy = this.code
              }else if(this.codeTab === 'trigger'){
                toCopy = this.triggerCode
              }
            this.$copyText(toCopy).then(() => {
              this.$message.success('')
            }).catch(() => {
              this.$message.error('')
            })
          },
          codeHelp(){
            this.$alert(`<ul>
              <li> - OW</li>
              <li> -  </li>
              <li> - OW</li>
            </ul>`, '', {
              dangerouslyUseHTMLString: true
            });
          },
          editSub(obj){
              const {data,key} = obj;
              this.parentData = {
                data: lodash.cloneDeep(this.data),
                key
              }
              this.dataReload(data)
          },
          returnProject(){
              this.ensureParent()
              this.dataReload(this.parentData.data)
              this.parentData = null
          },
          ensureParent(){
              if(this.parentData){
                this.parentData.data.childProjects.filter(item => item.key === this.parentData.key)[0].data = lodash.cloneDeep(this.data)
              }
          }
        },
    }
</script>
<style>
.el-dropdown-link {
  cursor: pointer;
  color: #409EFF;
}
.el-icon-arrow-down {
  font-size: 12px;
}
</style>
