<template>
  <el-dialog title="" :visible.sync="dialog" width="50%" :close-on-click-modal="false" :close-on-press-escape="false">
    <el-button type="text" @click="add"></el-button>
    <el-table :data="data">
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="text"  @click="editSubProject(scope.row,scope.$index)">{{scope.row.projectName}}</el-button>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" @click="changeName(scope.$index)"></el-button>
          <el-button size="mini" type="danger" @click="del(scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialog = false" type="primary"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import {getData} from "@/components/ef/data";
import { v4 as uuidv4 } from 'uuid';

export default {
  name:"SubProject",
  props:{
    data:Array
  },
  data(){
    return {
      dialog: false
    }
  },
  methods:{
    open(){
      this.dialog = true
    },
    add(){
      const i  = prompt('')
      if(i){
        this.data.push({
          projectName: i,
          data: getData(),
          key: uuidv4()
        })
      }

    },
    del(index){
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.data.splice(index,1)
      })
    },
    changeName(index){
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(({ value }) => {
        this.data[index].projectName = value
      })
    },
    editSubProject(row,index){
      this.$emit('editSub',{
        data:row.data,
        key:row.key
      })
      this.dialog = false
    }
  }
}
</script>
